// 
const gbs = {
    // /config/index.js -> proxyTable
    /*
     *  proxyTable: {
     *    '/static/data/': {
     *      target: 'http://xxxxxx.com',
     *      changeOrigin: true,
     *      pathRewrite: {
     *        '^/static/data/': ''
     *      }
     *    }
     *  },
     * */
    host: 'http://122.226.54.202:808/',
    // key
    db_prefix: 'sx_admin_',
  },
  /*  */
  STATUS_CODES = {
    201: {
      msg: '',
      action: () => {
      }
    },
    202: {
      msg: '',
      action: () => {
      }
    },
    204: {
      msg: '',
      action: () => {
      }
    },
    400: {
      msg: '',
      action: () => {
      }
    },
    401: {
      msg: '',
      action: function () {
        this.data.password = '';
      }
    },
    403: {
      msg: '',
      action: () => {
      }
    },
    404: {
      msg: '',
      action: () => {
      }
    },
    406: {
      msg: '',
      action: () => {
      }
    },
    410: {
      msg: '',
      action: () => {
      }
    },
    422: {
      msg: '',
      action: () => {
      }
    },
    500: {
      msg: '',
      action: () => {
      }
    }
  },
  // 
  cbs = {
    /**
     * ajax200
     * @param  {object} err 
     */
    statusError(err) {
      if (Object.keys(STATUS_CODES).includes(err.status + '')) {
        this.$message({
          showClose: true,
          message: STATUS_CODES[err.status].msg,
          type: 'error'
        });
        STATUS_CODES[err.status].action.call(this);
      }
      else {
        this.$message({
          showClose: true,
          message: ',!',
          type: 'error'
        });
        console.log(err)
      }
    },

    /**
     * ajax
     */
    requestError(err, url) {
      this.$message({
        showClose: true,
        message: `${url}!`,
        type: 'error'
      });
    }
  };

export {gbs, cbs}



